import React, { useState } from "react";
import NOT_BUILT_JSON_ARRAY from "../geo/notbuilt";
import built from "../geo/built";

const MapList = () => {
  const builtList = built.map((item) => ({ id: item.id, name: item.name }));
  const notBuildList = NOT_BUILT_JSON_ARRAY.map((item) => ({
    id: item.id,
    name: item.name,
  }));

  return (
    <div className="mapList">
      <MarkerList markers={builtList} className="built" />
      <MarkerList markers={notBuildList} className="not_built" />
    </div>
  );
};

const MarkerList = ({ markers, className }) => {
  return (
    <ul>
      {markers.map((marker) => (
        <ListItem key={marker.id} className={className} id={marker.id} name={marker.name} />
      ))}
    </ul>
  );
};

const ListItem = ({ id, name, className }) => {
  const [hovered, setHovered] = useState(false);

  const handleMouseOver = () => {
    setHovered(true);
  };

  const handleMouseOut = () => {
    setHovered(false);
  };

  return (
    <li
      key={id}
      className={className}
      value={id}
      style={{ listStyle: "none", color: hovered ? "red" : "black",cursor:"default" }}
      onMouseOver={handleMouseOver}
      onMouseOut={handleMouseOut}
    >
      {`${id}: ${name}`}
    </li>
  );
};

export default MapList;
